<template lang="pug">
  el-dialog(:title='title', v-model='modelShow', :size='size', @close='close', @open='open')
    slot
    .dialog-footer(slot='footer')
      el-button(@click='close')  
      el-button(type='primary', @click='confirm', :loading='loading')  
</template>

<script>
  import DialogVModelMixin from '@/mixins/DialogVModel'

  export default {
    props: {
      title: String,
      size: {
        type: String,
        default: 'small'
      },

      loading: {
        type: Boolean,
        default: false
      }
    },

    mixins: [DialogVModelMixin],
    methods: {
      confirm() {
        this.$emit('confirm')
      }
    }
  }
</script>



