<style lang='scss'>
  .my {
    margin-top: 100px;

    .button-wraper {
      float: right;
    }
  }
</style>

<template lang='pug'>
  .manage-page
    .mgmt-title 

    .dialogs
      revise-pd(
        v-model='revisePdDialogShow')

    el-row.my
      el-col(:span='12', :offset='6')
        info-bar(label='', :val='detail.employee_id' label-width="80px")
        el-form(:model='detail', :rules='rules' label-width="80px" ref="form")
          el-row
            el-col(:span='12')
              el-form-item(label='', prop="name")
                el-input(v-model='detail.name')
            el-col(:span='12')
              el-form-item(label='', prop="birthday")
                el-date-picker(
                  v-model='detail.birthday',
                  format='yyyy-MM-dd',
                  type='date')
          el-form-item(label='', prop="id_number")
            el-input(v-model='detail.id_number')
          el-row
            el-col(:span='12')
              el-form-item(label='', prop="gender_code")
                el-select(v-model='detail.gender_code', placeholder='')
                  el-option(label='', value='1')
                  el-option(label='', value='2')
            el-col(:span='12')
              el-form-item(label='', prop="cellphone")
                el-input(v-model='detail.cellphone')
          el-form-item(label='', prop="address")
            el-input(v-model='detail.address')
          el-form-item
            .button-wraper
              el-button(type='primary', @click='onSubmit') 
              el-button(type='primary', @click='onRevisePd') 
</template>

<script>
  import HttpService from '@/services/Http'
  import RevisePd from './Dialogs/RevisePd'
  import InfoBar from '@/components/InfoBar'
  import types from '@/store/types'
  import {isCellphone, isIdNumber} from '@/services/Validate'

  export default {
    components: {
      InfoBar,
      RevisePd
    },

    data() {
      return {
        detail: {},
        revisePdDialogShow: false,
        rules: {
          name: [{
            required: true, message: '', trigger: 'change'
          }],
          cellphone: [{
            required: true, message: '', trigger: 'change'
          }]
        }
      }
    },

    beforeRouteEnter(to, from, next) {
      HttpService.SCPrefixGet('staff/detail').then(data => {
        next(vm => {
          vm.detail = data
        })
      })
    },

    methods: {
      onSubmit() {
        console.log(this.$refs)
        this.$refs.form.validate(valid => {
          if (valid) {
            let data = Object.assign({}, this.detail, {
              // birthday: dateToDateStr(this.detail.birthday)
            })
            if (!data.gender_code.length) {
              data.gender_code = '0'
            }

            let errMsg = ''
            if (!isCellphone(data.cellphone)) {
              errMsg = ''
            } else if (data.id_number && !isIdNumber(data.id_number)) {
              errMsg = ''
            }
            if (errMsg) {
              this.$message(errMsg)
              return
            }

            HttpService.SCPrefixPost('staff/update', data).then(_ => {
              this.$message({
                type: 'success',
                message: ''
              })
              this.$store.dispatch(types.userInfo.UPDATE, {
                name: _.name
              })
            })
          } else {
            return false
          }
        })
      },

      onRevisePd() {
        this.revisePdDialogShow = true
      }
    }
  }
</script>



